export const addUser = (store, user) => {
  store.commit('ADD_USER', user)
}

export const addUsers = (store, users) => {
  store.commit('ADD_USERS', users)
}

export const deleteUser = (store, user) => {
  store.commit('DELETE_USER', user)
}

export const deleteUsers = (store, users) => {
  store.commit('DELETE_USERS', users)
}



