import Vue from 'vue'
let VueResource = require('vue-resource')

Vue.use(VueResource)
let ressource = Vue.resource('/users{/id}')

export const getUsers = (store) => {
  return ressource.get().then(response => {
    store.commit('ADD_USERS', response.data)
    store.commit('ADD_USERS2', response.data)
  })
}



