<template xmlns:v-on="http://www.w3.org/1999/xhtml">

    <div class="section row">

        <div class="col s12 m4">
            <ul class="collapsible" data-collapsible="accordion">
                <li>
                    <div class="collapsible-header"><span class="badge">7</span><i class="material-icons">work</i>Trier Par Département</div>
                    <div class="collapsible-body">
                        <p>
                            <input type="checkbox" id="msp"  v-model="checkMsp" v-on:click="actualizeUsers2()"/>
                            <label for="msp">Mathématiques et Sciences Physiques</label>
                        </p>
                        <p>
                            <input type="checkbox" id="gi" v-model="checkGi" v-on:click="actualizeUsers2()"/>
                            <label for="gi">Génie Informatique</label>
                        </p>
                        <p>
                            <input type="checkbox" id="gc"  v-model="checkGc" v-on:click="actualizeUsers2()"/>
                            <label for="gc">Génie Civil</label>
                        </p>
                        <p>
                            <input type="checkbox" id="gm" v-model="checkGm" v-on:click="actualizeUsers2()"/>
                            <label for="gm">Génie Mécanique</label>
                        </p>
                        <p>
                            <input type="checkbox" id="gind" v-model="checkGind" v-on:click="actualizeUsers2()"/>
                            <label for="gind">Génie Industriel</label>
                        </p>
                        <p>
                            <input type="checkbox" id="gtel" v-model="checkGtel" v-on:click="actualizeUsers2()"/>
                            <label for="gtel">Génie des Télécommunications</label>
                        </p>
                        <p>
                            <input type="checkbox" id="gele" v-model="checkGel" v-on:click="actualizeUsers2()"/>
                            <label for="gele">Génie Électrique</label>
                        </p>
                        <p>
                            <input type="checkbox" id="gmeteo" v-model="checkGme" v-on:click="actualizeUsers2()"/>
                            <label for="gmeteo">Génie Météorologique</label>
                        </p>
                    </div>
                </li>
                <li>
                    <div class="collapsible-header"><span class="badge">5</span><i class="material-icons">assessment</i>Trier par Niveaux</div>
                    <div class="collapsible-body">
                        <p>
                            <input type="checkbox" id="niv1" v-model="checkLevel1" v-on:click="actualizeUsers2()"/>
                            <label for="niv1">Niveau 1</label>
                        </p>
                        <p>
                            <input type="checkbox" id="niv2" v-model="checkLevel2" v-on:click="actualizeUsers2()" />
                            <label for="niv2">Niveau 2</label>
                        </p>
                        <p>
                            <input type="checkbox" id="niv3" v-model="checkLevel3" v-on:click="actualizeUsers2()"/>
                            <label for="niv3">Niveau 3</label>
                        </p>
                        <p>
                            <input type="checkbox" id="niv4" v-model="checkLevel4" v-on:click="actualizeUsers2()"/>
                            <label for="niv4">Niveau 4</label>
                        </p>
                        <p>
                            <input type="checkbox" id="niv5" v-model="checkLevel5" v-on:click="actualizeUsers2()"/>
                            <label for="niv5">Niveau 5</label>
                        </p>
                    </div>
                </li>
                <li>
                    <div class="collapsible-header"><span class="badge">6</span><i class="material-icons">view_headline</i>Trier par classe</div>
                    <div class="collapsible-body">
                        <p>
                            <input type="checkbox" id="a1" v-model="checkA1" v-on:click="actualizeUsers2()"/>
                            <label for="a1">A1</label>
                        </p>
                        <p>
                            <input type="checkbox" id="a2" v-model="checkA2" v-on:click="actualizeUsers2()"/>
                            <label for="a2">A2</label>
                        </p>
                        <p>
                            <input type="checkbox" id="a3" v-model="checkA3" v-on:click="actualizeUsers2()"/>
                            <label for="a2">A3</label>
                        </p>
                        <p>
                            <input type="checkbox" id="b1" v-model="checkB1" v-on:click="actualizeUsers2()" />
                            <label for="b1">B1</label>
                        </p><p>
                        <input type="checkbox" id="b2" v-model="checkB2" v-on:click="actualizeUsers2()"/>
                        <label for="b2">B2</label>
                    </p><p>
                        <input type="checkbox" id="b3" v-model="checkB3"  v-on:click="actualizeUsers2()"/>
                        <label for="b3">B3</label>
                    </p>
                    </div>
                </li>
            </ul>
        </div>

        <div class="col s12 z-depth-6 card-panel m7">
            <div class="preloader-wrapper big active" style="margin-left: 250px;" v-if="loader">
                <div class="spinner-layer spinner-blue-only">
                    <div class="circle-clipper left">
                        <div class="circle"></div>
                    </div><div class="gap-patch">
                    <div class="circle"></div>
                </div><div class="circle-clipper right">
                    <div class="circle"></div>
                </div>
                </div>
            </div>
            <span v-if="show" style="font-size:36px;color: blue"> Notification effectuée</span>
            <span v-if="error" style="font-size:28px;color: red"> Erreur la notification n'a pas été effectuée.</span>
            <form class="login-form">
                <div class="row margin">
                    <div class="col s12 m2">
                        <i class="material-icons medium">perm_identity</i>
                    </div>
                    <div  class="input-field col s12 m10">
                        <v-select multiple :options="options" v-model="selected"></v-select>
                    </div>

                </div>
                <div class="row margin">
                    <div class="col s12 m2">
                        <i class="material-icons medium">mode_edit</i>
                    </div>
                    <div class="input-field col s12 m10">
                        <textarea id="t" placeholder="Contenu du Message" class="materialize-textarea" v-model="msg"></textarea>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12 m4">
                        <p>
                            Type d'envoi :
                        </p>
                    </div>
                    <div class="input-field col s12 m4">
                        <p>
                            <input type="checkbox" id="test0" v-model="sendSms"/>
                            <label for="test0">SMS</label>
                        </p>
                    </div>
                    <div class="input-field col s12 m4">
                        <p>
                            <input type="checkbox" id="test" v-model="sendMail" />
                            <label for="test">Email</label>
                        </p>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <a class="btn waves-effect waves-light col s12" v-on:click="notifier()" >Notifier maintenant</a>
                    </div>
                </div>
            </form>
        </div>
    </div>

</template>


<script type="text/babel">
  import Vue from 'vue'
  import User from './User.vue'
  import vSelect from 'wtbf-vue-select'
  Vue.component('v-select', vSelect)
  import {mapActions, mapGetters} from 'vuex'
  let VueResource = require('vue-resource')

  Vue.use(VueResource)
  let ressource = Vue.resource('/notifications')
  export default {
    data () {
      return {
        loader: false,
        checkMsp: false,
        checkGi: false,
        checkGc: false,
        checkGind: false,
        checkGm: false,
        checkGtel: false,
        checkGel: false,
        checkGme: false,
        checkLevel1: false,
        checkLevel2: false,
        checkLevel3: false,
        checkLevel4: false,
        checkLevel5: false,
        checkA1: false,
        checkA2: false,
        checkA3: false,
        checkB1: false,
        checkB2: false,
        checkB3: false,
        sendSms: true,
        sendMail: true,
        selected: [],
        msg: '',
        test: [],
        show: false,
        error: false
      }
    },
    computed: {
      ...mapGetters(['users', 'users2']),
      options: function () {
        let options = []
        for (let i = 0; i < this.users.length; i++) {
          let user = this.users[i]
          options.push({label: user.name, value: user.id})
        }
        return options
      }
    },
    watch: {
      msg: function (val) {
        this.show = false
        this.error = false
      }
    },
    methods: {
      ...mapActions([
        'getUsers',
        'deleteUsers',
        'deleteUser',
        'addUser',
        'addUsers'
      ]),
      actualizeUsers2: function () {
        let urs = this.users2
        this.deleteUsers(urs)
        for (let i = 0; i < urs.length; i++) {
          let user = urs[i]
          if (!(this.checkLevel1 === this.checkLevel2 && this.checkLevel2 === this.checkLevel3 && this.checkLevel3 === this.checkLevel4 && this.checkLevel4 === this.checkLevel5 && this.checkLevel5 === false)) {
            if (((this.checkLevel1 && user.level === 1) || (this.checkLevel2 && user.level === 2) || (this.checkLevel3 && user.level === 3) || (this.checkLevel4 && user.level === 4) || (this.checkLevel5 && user.level === 5))) {
              let index = this.users.findIndex((u) => u.id === user.id)
              if (index === -1) {
                this.addUser(user)
                console.log('tata1' + i)
              }
            }
          } else {
            this.deleteUsers(urs)
            this.addUsers(urs)
            console.log('tata2')
          }
        }

        let p = this.users.length
        for (let i = 0; i < p; i++) {
          let user = this.users[i]
          if (!(this.checkGi === this.checkGm && this.checkGm === this.checkGc && this.checkGc === this.checkGme && this.checkGme === this.checkGind && this.checkGind === this.checkGtel && this.checkGtel === this.checkel && this.checkel === this.checkMsp && this.checkMsp === true)) {
            if (((this.checkGi && user.department === 'Info') || (this.checkGm && user.department === 'Meca') || (this.checkGc && user.department === 'Gc') || (this.checkGme && user.department === 'Gme') || (this.checkGind && user.department === 'Gind') || (this.checkGtel && user.departemt === 'Gtel') || (this.checkel && user.departemt === 'Gel') || (this.checkMsp && user.departemt === 'Msp'))) {
              let index = this.users.findIndex((u) => u.id === user.id)
              if (index === -1) {
                this.addUser(user)
                console.log('papa1' + i)
              }
            }
          } else {
            console.log('papa2')
          }
        }

        let c = this.users.length
        for (let i = 0; i < c; i++) {
          let user = this.users[i]
          if (!(this.checkA1 === this.checkA2 && this.checkA2 === this.checkA3 && this.checkA3 === this.checkB1 && this.checkB1 === this.checkB2 && this.checkB2 === this.checkB3 && this.checkB3 === false)) {
            if (((this.checkA1 && user.class === 'A1') || (this.checkA2 && user.class === 'A2') || (this.checkA3 && user.class === 'A3') || (this.checkB1 && user.class === 'B1') || (this.checkB2 && user.class === 'B2') || (this.checkB3 && user.class === 'B3'))) {
              let index = this.users.findIndex((u) => u.id === user.id)
              if (index === -1) {
                this.addUser(user)
                console.log('tonton1' + i)
              }
            }
          } else {
            console.log('tonton2')
          }
        }
        return false
      },
      notifier: function () {
        this.loader = true
        let ids = []
        for (let i = 0; i < this.selected.length; i++) {
          ids[i] = (this.selected[i].value)
        }
        let urs = {ids: ids, message: this.msg, sendMail: this.sendMail, sendSms: this.sendSms}
        this.test = urs
        console.log('called called')
        ressource.save(urs).then(response => {
          this.show = true
          this.loader = false
        }, response => {
          console.log(response)
          this.error = true
          this.loader = false
        })
      }
    },
    components: {
      User,
      'vue-select': require('vue-select')
    },
    mounted () {
      this.getUsers().then(response => {
        this.loading = false
      })
    }
  }
</script>



