import {getUsers} from '../getters'
import {addUser, addUsers, deleteUsers, deleteUser} from '../actions'

export const state = {
  users: [],
  users2: []
}

export const getters = {
  users: (state) => state.users,
  users2: (state) => state.users2
}

export const mutations = {
  ADD_USER (state, user) {
    state.users.push(user)
  },
  ADD_USERS (state, users) {
    state.users.push(...users)
  },
  ADD_USERS2 (state, users) {
    state.users2.push(...users)
  },
  DELETE_USER (state, user) {
    let index = state.users.findIndex((u) => u.id === user.id)
    state.users.splice(index, 1)
  },
  DELETE_USERS (state, users) {
    users.forEach(function (user) {
      let index = state.users.findIndex((u) => u.id === user.id)
      state.users.splice(index, 1)
    })
  }
}

export const actions = {
  addUser,
  addUsers,
  deleteUsers,
  deleteUser,
  getUsers
}

export default {
  state,
  mutations,
  getters,
  actions
}



