import Vue from 'vue'
import Users from './components/Users.vue'
let VueResource = require('vue-resource')
import store from './store/store'

require('./css/animation.css')
Vue.use(VueResource)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  components: {Users}
})



